/* Container for the entire game area */
.gameDiv {
    min-height: 100vh; /* Full viewport height */
    width: 100%;
    display: flex; /* Flexbox layout */
    flex-direction: column; /* Arrange items in a column */
    justify-content: center; /* Center items vertically */
    align-items: center; /* Center items horizontally */
    gap: 30px; /* Space between items */
}

/* Div for tracking the question number or progress */
.questionTrackerDiv {
    position: absolute; /* Positioned relative to the nearest ancestor */
    z-index: 200; /* Ensure it appears above other elements */
    top: 0; /* Align to the top of the viewport */
    right: 0; /* Align to the right of the viewport */
    margin: 25px; /* Spacing from the edges */
    font-size: 25px; /* Larger font size */
}

/* Container for the question text */
.questionDiv {
    width: 100%; /* Full width */
    display: flex; /* Flexbox layout */
    justify-content: center; /* Center the question horizontally */
}

/* Styling for the question text */
.question {
    margin: 0; /* Remove default margin */
    font-size: 30px; /* Larger text size for emphasis */
    max-width: 70%; /* Limit width for readability */
    text-align: center; /* Center align text */
}

/* Container for open-ended question inputs */
.openEndedQuestionsDiv {
    display: flex; /* Flexbox layout */
    flex-direction: column; /* Arrange items vertically */
    align-items: center; /* Center items horizontally */
    gap: 20px; /* Space between items */
    text-transform: uppercase;
}

/* Send button styling */
.sendButton {
    width: 100px; /* Fixed width */
    all: unset; /* Reset all default styles */
    border: 1px solid silver; /* Silver border */
    padding: 10px 10px; /* Padding for clickable area */
    text-align: center; /* Center text */
    border-radius: 5px; /* Rounded corners */
}

/* Hover effect for the send button */
.sendButton:hover {
    cursor: pointer; /* Change cursor to pointer on hover */
}

/* Container for multiple-choice answers */
.answers {
    display: flex; /* Flexbox layout */
    gap: 30px; /* Space between answer buttons */
    flex-wrap: wrap; /* Wrap items to the next line if needed */
    justify-content: center; /* Center items */
    max-width: 800px; /* Limit width of the answer area */
}

/* Input field for open-ended questions */
.openQuestionInput {
    all: unset; /* Reset all default styles */
    border: 1px solid silver; /* Silver border */
    padding: 10px 20px; /* Padding for typing area */
    border-radius: 5px; /* Rounded corners */
}

/* Special styling for the quit game button */
.quitGameButton {
    font-size: 20px;
    border: 1px solid black;
    color: black;
    background-color: silver;
    padding: 10px 20px;
    border-radius: 5px;
}

/* Game over section */
.gameOver {
    display: flex; /* Flexbox layout */
    flex-direction: column; /* Arrange items vertically */
    padding: 30px 0; /* Vertical padding */
    gap: 30px; /* Space between items */
}

/* Styling for game over heading */
.gameOver h2 {
    text-align: center; /* Center align text */
    font-size: 40px; /* Larger font size */
}

/* Score display text */
.scoreDisplay {
    text-align: center; /* Center align text */
    font-size: 25px; /* Larger font size */
    margin-top: 60px; /* Space above the score */
}

/* Container for the restart button */
.restartGameDiv {
    display: flex; /* Flexbox layout */
    justify-content: center; /* Center the button */
}
