body{
    padding: 0;
    margin: 0;
    font-family: sans-serif;
    background-color: black;
    color: silver;
}

/* light mode */
.app.light {
    background-color: white;
    color: #000000;
}

.lightIcon{
    color: black;
}

/* Dark Mode */
.app.dark {
background-color: black;
color: silver;
}

.darkIcon{
    color: white;
}

.themeToggleButton{
    position: absolute;
    background: none;
    border: none;
    margin: 20px;
}

/* Styling for reusable buttons */
.stylizedButton {
    all: unset; /* Reset all default styles */
    font-size: 20px; /* Text size */
    border: 1px solid silver; /* Silver border */
    padding: 10px 20px; /* Padding for clickable area */
    border-radius: 5px; /* Rounded corners */
    width: 300px; /* Fixed width */
    text-align: center; /* Center text */
}

/* Hover effect for stylized buttons */
.stylizedButton:hover {
    cursor: pointer; /* Change cursor to pointer on hover */
}

.stylizedButton:active {
    background-color: silver; /* Change background to silver */
    color: black; /* Change text color to black */
}